.ready2 {
  width: 55.9px;
  position: relative;
  font-weight: 500;
  display: flex;
  align-items: center;
}
.task-title2 {
  align-self: stretch;
  position: relative;
  font-size: 20px;
  font-weight: 300;
  color: #000;
}
.ready-parent {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 6px;
  color: #25cb55;
}
.survey-farm-at2 {
  width: 146.7px;
  height: 0;
  display: none;
}
.recipe21,
.survey-farm-at2,
.timestamp2 {
  position: relative;
  font-weight: 500;
  align-items: center;
}
.recipe21 {
  width: 222.3px;
  height: 0;
  font-size: 14px;
  font-family: Inter;
  color: #000;
  display: none;
}
.timestamp2 {
  width: 40.7px;
  display: flex;
}
.progress-bar2,
.songbar2 {
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 2px;
  background-color: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
}
.progress-bar2 {
  background-color: rgba(0, 0, 0, 0.6);
  width: 3.5px;
  height: 4px;
  z-index: 1;
}
.progress2 {
  align-self: stretch;
  height: 4px;
  position: relative;
}
.progress-info,
.task-progress2 {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
}
.progress-info {
  align-self: stretch;
  gap: 4px;
  text-align: right;
}
.task-progress2 {
  flex: 1;
  gap: 19px;
  min-width: 249px;
  max-width: 100%;
  text-align: left;
  font-size: 15px;
  color: rgba(36, 31, 31, 0.4);
  font-family: Montserrat;
}
@media screen and (max-width: 450px) {
  .task-title2 {
    font-size: 16px;
  }
}
