.device-box-large201 {
  align-self: stretch;
  flex: 1;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  flex-direction: column;
  align-items: center;
  padding: 56px 20px;
  gap: 55px;
  max-width: 100%;
  text-align: left;
  font-size: 12px;
  color: #000;
  font-family: Inter;
  position: relative;
  overflow: hidden;
  transition: transform 0.3s ease-out, box-shadow 0.3s ease-out;
}

.device-box-large201::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 150%;
  height: 150%;
  background: radial-gradient(circle closest-side, rgba(0, 150, 255, 0.5), rgba(0, 150, 255, 0) 75%);
  transform: translate(-50%, -50%) scale(0);
  transition: transform 0.5s ease-out;
  z-index: -1;
}

.device-box-large201:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.device-box-large201:hover::before {
  transform: translate(-50%, -50%) scale(1);
}

/* Optional: Adjusting text styles or other content inside */
.device-box-large201 h2 {
  margin: 0;
  font-size: 1.5rem;
}

.device-box-large201 p {
  margin: 0;
  font-size: 1rem;
}

@media screen and (max-width: 750px) {
  .device-box-large201 {
    padding-top: 908px;
    padding-bottom: 36px;
    box-sizing: border-box;
  }
}
