}

.xiaomi-robot-vacuum-mop-2-pr-container::after {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  width: 0;
  height: 100%;
  background: linear-gradient(to right, rgba(0, 149, 255, 0.124), rgba(0, 150, 255, 0.124));
  pointer-events: none;
  transform: translateX(-50%);
  transition: width 0.5s ease-in-out;
  z-index: 0;
}

.xiaomi-robot-vacuum-mop-2-pr-container:hover::after {
  width: 100%;
}

.xiaomi-robot-vacuum-mop-2-pr-container h2 {
