.content-inner,
.menu-bar-wrapper {
  display: flex;
  align-items: flex-start;
}
.menu-bar-wrapper {
  height: 960px;
  width: 96px;
  flex-direction: column;
  justify-content: flex-start;
  padding: 0 6px 0 0;
  box-sizing: border-box;
}
.content-inner {
  align-self: stretch;
  flex-direction: row;
  justify-content: flex-end;
  max-width: 100%;
  margin-top: -888px;
}
.content,
.content-wrapper {
  flex-direction: column;
  max-width: 100%;
}
.content {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0%;
}
.content-wrapper {
  width: 644px;
  padding: 54px 0 0;
}
.add-device-4,
.content-wrapper,
.device-information-wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
}
.device-information-wrapper {
  flex: 1;
  flex-direction: column;
  padding: 56px 0 0;
  min-width: 456px;
  max-width: 100%;
}
.add-device-4 {
  width: 100%;
  position: relative;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  background-color: #fff;
  flex-direction: row;
  padding: 0 18px 0 0;
  gap: 1px;
  line-height: normal;
  letter-spacing: normal;
}
@media screen and (max-width: 1125px) {
  .add-device-4 {
    flex-wrap: wrap;
    padding-left: 18px;
    padding-top: 18px;
    padding-bottom: 18px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 1050px) {
  .content-wrapper {
    padding-top: 35px;
    box-sizing: border-box;
  }
  .device-information-wrapper {
    padding-top: 36px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 750px) {
  .content-wrapper,
  .device-information-wrapper {
    padding-top: 23px;
    box-sizing: border-box;
  }
  .device-information-wrapper {
    min-width: 100%;
  }
}
