.logo-background1 {
  position: absolute;
  top: -16px;
  left: -20px;
  border-radius: 0 10px 10px 0;
  background-color: #000;
  width: 85px;
  height: 63px;
}

.logo-icon1 {
  position: absolute;
  height: 67.04%;
  width: 89.79%;
  top: 22.22%;
  right: 2.08%;
  bottom: 10.74%;
  left: 8.13%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}

.logo-box4 {
  height: 27px;
  width: 48px;
  position: relative;
  border-radius: 0 30px 0 0;
}

.logo-box-wrapper2 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 21px 0 20px;
}

.image-icon {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 0 10px 10px 0;
  background-color: rgba(44, 44, 44, 0.66);
}

.nav-icon1 {
  position: absolute;
  height: 58.33%;
  width: 42.12%;
  top: 20%;
  right: 27.76%;
  bottom: 21.67%;
  left: 30.12%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}

.home-nav-icon4-btn:hover {
  background-color: rgba(255, 255, 255, 0.081);
}

.home-nav-icon4 {
  align-self: stretch;
  height: 60px;
  position: relative;
  cursor: pointer;
}

.add-device-icon4,
.log-nav-icon4,
.recipe-nav-icon4,
.support-nav-icon4 {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}

.center-icons4 {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 5px 0 0;
  gap: 25px;
}

.profile-nav-icon-child2 {
  position: absolute;
  top: -11px;
  left: -25px;
  background-color: rgba(171, 171, 171, 0.66);
  width: 97px;
  height: 58px;
  display: none;
}

.profile-nav-icon9 {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}

.profile-nav-icon8 {
  height: 35px;
  width: 28px;
  position: relative;
}

.menu-bar4,
.profile-nav-icon-wrapper2 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 31px 0 30px;
}

.menu-bar4 {
  height: 960px;
  width: 90px;
  border-radius: 0 30px 0 0;
  background-color: #000;
  flex-direction: column;
  padding: 50px 0 88px;
  box-sizing: border-box;
  gap: 180px;
}

.btn2:hover {
  background-color: rgba(255, 255, 255, 0.081);
}

@media screen and (max-width: 1125px) {
  .menu-bar4 {
    display: none;
  }
}

@media screen and (max-width: 1050px) {
  .menu-bar4 {
    padding-top: 32px;
    padding-bottom: 57px;
    box-sizing: border-box;
  }
}

@media screen and (max-width: 450px) {
  .menu-bar4 {
    padding-top: 21px;
    padding-bottom: 37px;
    box-sizing: border-box;
  }
}