  left: -20px;
  border-radius: 0 10px 10px 0;
  background-color: #000;
  width: 85px;
  height: 63px;
}
.logo-icon {
  position: absolute;
  height: 67.04%;
  width: 89.79%;
  top: 22.22%;
  right: 2.08%;
  bottom: 10.74%;
  left: 8.13%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}
.logo-box3 {
  height: 27px;
  width: 48px;
  position: relative;
  right: 10px;
  border-radius: 0 30px 0 0;
}
.logo-box-wrapper1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 21px 0 20px;
}
.nav-icon-background {
  position: absolute;
  height: 100%;
  width: 100%;
