  left: 30.12%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  z-index: 1;
}
.home-nav-icon3 {
  align-self: stretch;
  height: 60px;
  position: relative;
}
.add-device-icon3,
.log-nav-icon3,
.recipe-nav-icon3,
.support-nav-icon3 {
  align-self: stretch;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.center-icons3 {
  align-self: stretch;
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 5px 0 0;
  gap: 25px;
}
.profile-nav-icon-child1 {
  position: absolute;
  top: -11px;
  left: -25px;
  background-color: rgba(171, 171, 171, 0.66);
  width: 97px;
  height: 58px;
  display: none;
}
.profile-nav-icon7 {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.profile-nav-icon6 {
  height: 35px;
  width: 28px;
  position: relative;
}
.menu-bar3,
.profile-nav-icon-wrapper1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 31px 0 30px;
}
.menu-bar3 {
  height: 960px;
  width: 90px;
  border-radius: 0 30px 0 0;
  background-color: #000;
  flex-direction: column;
  padding: 50px 0 88px;
  box-sizing: border-box;
  gap: 180px;
}

.btn:hover{
  background-color: rgba(255, 255, 255, 0.081);
}

@media screen and (max-width: 800px) {
  .menu-bar3 {
    padding-top: 32px;
    padding-bottom: 57px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .menu-bar3 {
    padding-top: 21px;
    padding-bottom: 37px;
    box-sizing: border-box;
  }
}

