/* Modal.css */

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.modal-container {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 1194px;
  height: 609px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.modal-header {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 16px;
}

.modal-body {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 16px;
}

.modal-body p {
  margin-bottom: 16px;
}

.modal-section {
  margin-bottom: 16px;
}

.modal-section h2 {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 8px;
}

.modal-section ul {
  padding-left: 20px;
}

.modal-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
}

.checkbox-label {
  display: flex;
  align-items: center;
}

.btn-next {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
}

.btn-next:hover {
  background-color: #218838;
}



/* ExecuteButton1.css */
.modal-enter {
  opacity: 0;
  transform: scale(0.9);
}
.modal-enter-active {
  opacity: 1;
  transform: scale(1);
  transition: opacity 300ms, transform 300ms;
}
.modal-exit {
  opacity: 1;
  transform: scale(1);
}
.modal-exit-active {
  opacity: 0;
  transform: scale(0.9);
  transition: opacity 300ms, transform 300ms;
}

/* Toastify overrides */
.Toastify__toast--error {
  background: #f44336;
  color: white;
}

